.today{
	width:100% ; 
	height:100%;
	padding:20px; 
	text-align: left;
}
.mainInfo{
	display:grid;
	grid-template: 'datetime temperature'
					'city temperature';
	font-size: 26px;
	margin-bottom: 10px;
	
}
.datetime{
	grid-area: datetime;
}
.temperature{
	grid-area: temperature;
	display: flex;
	justify-content: center;
	align-content: center;
}
.temperature img{
	width: 50px;
	height: 50px;
}
.temperature div{
	font-size: 30px;
	align-self: center;
}
